import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;
const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;
const StyledPic = styled.div`
  position: relative;
  max-width: 300px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: var(--green);

    &:hover,
    &:focus {
      outline: 0;
      transform: translate(-4px, -4px);

      &:after {
        transform: translate(8px, 8px);
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
      mix-blend-mode: multiply;
      filter: grayscale(100%) contrast(1);
      transition: var(--transition);
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: var(--navy);
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid var(--green);
      top: 14px;
      left: 14px;
      z-index: -1;
    }
  }
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  const skills = ['Python', 'C++', 'Java', 'JavaScript', 'Node.js', 'HTML', 'CSS', 'SQL Developer', 'IntelliJ IDEA', 'Android Studio', 'Git', 'Sage', 'SPARQL', 'GraphDB', 'Unity', 'Godot' ];

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>

<nav>
   <ul>
      <li><a href="LettreAmine.pdf" download> Tlchargez Ma Lettre De Motivation</a></li>
   </ul>
</nav>


      <div className="inner">
        <StyledText>
          <div>

          <p>tudiant en deuxime anne de <strong>BUT Informatique</strong>, je suis  la recherche dun 
          <strong>stage en dveloppement logiciel</strong> afin dapprofondir mes comptences en 
          <strong>programmation, gestion de bases de donnes</strong> et <strong>scurit informatique</strong>.  
          </p>
<p>
  Passionn par les nouvelles technologies, je fais preuve de <strong>rigueur</strong>, 
  d<strong>adaptabilit</strong> et dune grande <strong>curiosit</strong>. Je suis motiv  minvestir 
  pleinement dans des projets varis et innovants au sein de votre entreprise.  
</p>
<h3>Mon Projet Acadmique et Professionnel</h3>
            <p> 
              Je souhaite poursuivre ma formation en effectuant la troisime anne afin dapprofondir 
              mes comptences et dobtenir mon diplme.
              Toujours en qute de nouvelles expriences enrichissantes, 
              je suis galement ouvert  lide de raliser cette dernire anne  ltranger, 
              notamment au Canada, afin de dcouvrir un nouvel environnement acadmique et culturel 
              tout en dveloppant mon expertise en informatique.
              Aprs lobtention de mon BUT, mon objectif est dintgrer une cole dingnieurs pour me 
              spcialiser dans un domaine qui me passionne : la cyberscurit. Ce secteur en constante 
              volution reprsente pour moi un dfi stimulant, o je pourrai mettre en pratique 
              mes connaissances en dveloppement, en gestion des systmes et en scurit des rseaux.
              Grce  cette formation, je souhaite acqurir une expertise avance pour contribuer 
               la protection des infrastructures numriques et  la lutte contre les cybermenaces, 
              tout en voluant dans un domaine qui me motive profondment.
            </p>

            <p>
            Tout au long de mon parcours en informatique, jai eu loccasion de travailler avec une
            large gamme doutils et de technologies. En programmation, jai utilis des langages tels
            que Python, C++, Java et JavaScript pour dvelopper diverses applications. En dveloppement web, 
            jai acquis de lexprience avec HTML, CSS ainsi que SQL Developer pour la gestion des bases de donnes.
            Jai galement manipul des environnements et outils professionnels, notamment Sage pour la gestion comptable, 
            IntelliJ IDEA et Android Studio pour le dveloppement dapplications, ainsi que Git pour le versioning et la 
            collaboration sur des projets. En modlisation et gestion de donnes, jai utilis SPARQL et GraphDB.
            Enfin, dans le domaine du jeu vido et des simulations interactives, jai eu lopportunit de travailler 
            avec des moteurs tels que Unity et Godot. Ces expriences mont permis de dvelopper une approche 
            polyvalente et une capacit dadaptation aux diffrentes technologies du secteur.
            </p>

            <p>Voici les technologies avec lesquelles j'ai rcemment travaill :</p>
          </div>

          <ul className="skills-list">
            {skills && skills.map((skill, i) => <li key={i}>{skill}</li>)}
          </ul>
        </StyledText>

        <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
        </StyledPic>
      </div>
    </StyledAboutSection>
  );
};

export default About;
